@use "../utils" as *;

body {
	@include body-1;
	letter-spacing: -0.025em;
	word-break: keep-all;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
span,
a,
input,
button,
ul,
li {
	font-family: "Pretendard", Pretendard, -apple-system, BlinkMacSystemFont,
		system-ui, Roboto, "Helvetica Neue", "Segoe UI", "Apple SD Gothic Neo",
		"Noto Sans KR", "Malgun Gothic", "Apple Color Emoji", "Segoe UI Emoji",
		"Segoe UI Symbol", sans-serif;
}

ul,
ol {
	padding: 0;
	margin: 0;
	list-style: none;
}

.main {
	color: #ffffff;
	background-color: #141414;
	min-height: 100vh;
	& > .ct-section-inner-wrap {
		width: 100%;
		padding: 0;
	}
}

.section {
	& > .ct-section-inner-wrap {
		@include flex($direction: column);
		@include calc-column-width(12);
		margin: 0 auto;
		padding: 160px 0;
	}
	&__cols-10 {
		& > .ct-section-inner-wrap {
			@include calc-column-width(10);
		}
	}
}

.headline-box {
	@include calc-column-width(6);
	margin-right: auto;
	&__index {
		color: $primary-color;
		@include font-monument;
	}
	&__head {
		font-size: 48px;
		font-weight: bold;
		line-height: 1.3;
		padding: 0.5em 0;
	}
	&__body {
		@include body-1;
	}
}
