body {
  letter-spacing: -.025em;
  word-break: keep-all;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
  font-weight: normal;
  line-height: 1.6em;
}

h1, h2, h3, h4, h5, h6, p, span, a, input, button, ul, li {
  font-family: Pretendard, -apple-system, BlinkMacSystemFont, system-ui, Roboto, Helvetica Neue, Segoe UI, Apple SD Gothic Neo, Noto Sans KR, Malgun Gothic, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, sans-serif;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

.main {
  color: #fff;
  background-color: #141414;
  min-height: 100vh;
}

.main > .ct-section-inner-wrap {
  width: 100%;
  padding: 0;
}

.section > .ct-section-inner-wrap {
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: calc(100% - 30px);
  max-width: 1110px;
  margin: 0 auto;
  padding: 160px 0;
  display: flex;
}

.section__cols-10 > .ct-section-inner-wrap {
  width: calc(100% - 30px);
  max-width: 920px;
}

.headline-box {
  width: calc(100% - 30px);
  max-width: 540px;
  margin-right: auto;
}

.headline-box__index {
  color: #d91a83;
  font-family: PPMonumentExtended, -apple-system, BlinkMacSystemFont, system-ui, Roboto, Helvetica Neue, Segoe UI, Apple SD Gothic Neo, Noto Sans KR, Malgun Gothic, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, sans-serif;
}

.headline-box__head {
  padding: .5em 0;
  font-size: 48px;
  font-weight: bold;
  line-height: 1.3;
}

.headline-box__body {
  font-size: 16px;
  font-weight: normal;
  line-height: 1.6em;
}

.package-slider__card {
  background-color: #fff;
  width: 540px;
}

.package-slider__card--inactive {
  display: none;
}

.package-slider__card a {
  color: #111;
}

.package-slider__card h3, .package-slider__card h3 > span {
  text-transform: capitalize;
  font-family: PPMonumentExtended, -apple-system, BlinkMacSystemFont, system-ui, Roboto, Helvetica Neue, Segoe UI, Apple SD Gothic Neo, Noto Sans KR, Malgun Gothic, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, sans-serif;
  font-size: 28px;
  font-weight: 700;
}

.package-slider__controllers {
  justify-content: "space-between";
  flex-direction: row;
  align-items: center;
  width: calc(100% - 30px);
  max-width: 920px;
  margin: 0 auto;
  padding-bottom: 80px;
  display: flex;
}

.filter-model {
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  gap: 14px;
  display: flex;
}

.filter-model__item {
  opacity: .7;
  cursor: pointer;
  font-size: 18px;
  font-weight: bold;
}

.filter-model__item--active {
  opacity: 1;
}

.price-tag__item {
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  display: flex;
}
